import React, { useContext,useState } from 'react'
import { AppContext } from '../Context';
import CartItem from './components/CartItem';

const Cart = () => {
  const{count,itemTitle,itemPrice,itemRating}  = useContext(AppContext);

  const [total, settotal] = useState(0);
  //settotal(itemPrice + total);


 

  return (
    <div>
      <h3 className="cart-title">Cart</h3>
      {
      itemTitle.map((element,idx) =>
        (<CartItem element={element} price = {itemPrice[idx]} item></CartItem>)
       
      
      )
      }
      <div className="total">
      <p>{total}</p>
      </div>

    </div>
  )
}

export default Cart
