import React, { useContext, useState } from 'react'
import { AppContext } from '../../Context';

const ProductCard = ({title,image,price,rating,description}) => {
  const{count,setcount,setitemTitle,setitemPrice,setitemRating,itemTitle,itemPrice}  = useContext(AppContext);

  
function addToCart(){
  // console.log(title);
  console.log(count);
  itemTitle.push(title);
  itemPrice.push(price);
  console.log(itemPrice);
  setitemTitle(itemTitle);
  setitemPrice(itemPrice);
  // setitemPrice(price);
  // setitemRating(rating.rate);
  setcount(count+1);

}


  return (
    <div className ='card'>
      <img src={image} alt="product" />
      <h5>{title}</h5>
      {/* <p>{description}</p> */}
    
     <div className="price-rating">
     <p>Rs.{price} </p>
     <span className='rate'>{rating.rate}/5</span>
     </div>
     
       
    
     
     <button onClick={()=>addToCart()} className='cart'>Add to Cart</button>

    </div>
  )
}

export default ProductCard
