import React, { useContext, useState } from 'react'
import { AppContext } from '../../Context';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronUp,faChevronDown } from '@fortawesome/free-solid-svg-icons'
const CartItem = ({element,price}) => {
  const{count,setcount,setitemTitle,setitemPrice,setitemRating,itemTitle,itemPrice}  = useContext(AppContext);
  
  const [quantity, setquantity] = useState(1);

  function increase(){
    setquantity(quantity+1);
  }
  function decrease(){
    setquantity(quantity-1);
  }

  return (
    <>
    <div className="cartItem">
      <div className="itemName">
        <p>{element}</p>
      </div>
      <div className="price">
        <p>{price*quantity}</p>
      </div>
      <div className="quantity">
        <p>{quantity}</p>
      </div>
      <div className="incdec">
        <button onClick={increase} className='arrow'>
        <i><FontAwesomeIcon icon={faChevronUp} /></i>
        </button>
        <button onClick={decrease} className='arrow'>
        <i><FontAwesomeIcon icon={faChevronDown} /></i>
        </button>
        
      </div>
    </div>


  </>
  )
}

export default CartItem
